
import {
GetActivityDetails
} from 'src/Api/api'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Panel from 'vux/src/components/panel/index.vue'


export default {
name: 'activity-details',
components: {
XButton,
Box,
GroupTitle,
Group,
Flexbox,
FlexboxItem,
Divider,
Panel
},
data () {
return {
activity: {},
activityEnrollUsersNumber: 0
}
},
created () {
this.GetList()
},
methods: {
GetList () {
GetActivityDetails({
openid: this.$store.state.user.token,
id: this.$route.params.id
}).then(response => {
console.log('GetActivity', response.data)
this.activity = response.data.data
this.activityEnrollUsersNumber = this.activity.activityEnrollUsers.length
})
},
GoActivityDetails (id) {
this.$router.push(`/activitydetails/${id}`)
},
numberConvertToUppercase (num) {
num = Number(num)
var upperCaseNumber = ['零', '一', '二', '三', '四', '五', '六', '七', '八', '九', '十', '百', '千', '万', '亿']
var length = String(num).length
if (length === 1) {
return upperCaseNumber[num]
} else if (length === 2) {
if (num === 10) {
return upperCaseNumber[num]
} else if (num > 10 && num < 20) {
return '十' + upperCaseNumber[String(num).charAt(1)]
} else {
return upperCaseNumber[String(num).charAt(0)] + '十' + upperCaseNumber[String(num).charAt(1)].replace('零', '')
}
}
}
}
}



